import React from 'react'
import { connect } from "react-redux";
import { addArticle } from './actions/index';
import uuidv1 from "uuid";
import CKEditor from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';

function mapDispatchToProps(dispatch) {
  return {
    addArticle: article => dispatch(addArticle(article))
  };
}



class ConnectedCreateTopics extends React.Component {
  constructor() {
    super();
    this.state = {
      articleHeading: "",
      articleDescription: "This is ck editor"
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  handleSubmit(event) {
    event.preventDefault(); 
    const { articleHeading, articleDescription } = this.state;
    const _id = uuidv1();
    this.props.addArticle({ articleHeading, _id , articleDescription});
    this.setState({ articleHeading: "" });
  }

  handleChange(event) {
    this.setState({ articleHeading : event.target.value });
    console.log(this.state);
  }

    render() {
        const { articleHeading, articleDescription} = this.state;
        return (
          <form onSubmit={this.handleSubmit}>
            <div className="form-group">
              <div><label htmlFor="title">Title</label>
              <input
                type="text"
                className="form-control"
                id="title"
                value={articleHeading}
                onChange={this.handleChange}
              />
              </div>
              <div className="m2-top">
              <CKEditor
                    editor={ ClassicEditor }
                    data={articleDescription}
                    onInit={ editor => {
                        // You can store the "editor" and use when it is needed.
                        console.log( 'Editor is ready to use!', editor );
                    } }
                    onChange={ ( event, editor ) => {
                        const data = editor.getData();
                        this.state.articleDescription = data;
                        //console.log( { event, editor, data } );
                    } }
                    onBlur={ ( event, editor ) => {
                        console.log( 'Blur.', editor );
                    } }
                    onFocus={ ( event, editor ) => {
                        console.log( 'Focus.', editor );
                    } }
                />
              </div>
            </div>
            <button type="submit" className="btn btn-success btn-lg">
              SAVE
            </button>
          </form>
        );
      }
}

const CreateTopics = connect(null, mapDispatchToProps)(ConnectedCreateTopics);
export default CreateTopics