import { makeStyles } from '@material-ui/core';
import React from 'react'

class Profile extends React.Component {

    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        return (
            <div>
                <div className='mainInfo'>
                    <div><img className='profileImage' src="/images/my_image.png"></img></div>
                    <div className='profileInfo'>
                        <p><strong>About Me : </strong></p>
                        <p>Experienced Software Engineer having good command over technical consulting and product development. </p>
                        <p>Experienced in Web Development, System Design, Algorithms , various design patterns, Object oriented design</p>
                        <p> <strong>Technical Skills: </strong></p>
                        <p>FrontEnd :  Angular, Javascript,  HTML, CSS3, Redux</p>
                        <p>Backend :  Nodejs, GRAPHQL, MSSQL, ORACLE PLSQL</p>

                    </div>
                </div>
            </div>
        )
    }
}
export default Profile