import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import Home from './../home';
import Resume from './../resume';
import NotFound from './../not-found';
import Topics from './../topics';
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";
import CreateTopics from './../creat-topic';
import Profile from '../profile';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    appBar: {
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    appBarShift: {
        width: `calc(100% - ${drawerWidth}px)`,
        marginLeft: drawerWidth,
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    hide: {
        display: 'none',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
    },
    drawerPaper: {
        width: drawerWidth,
    },
    drawerHeader: {
        display: 'flex',
        alignItems: 'center',
        padding: theme.spacing(0, 1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
        justifyContent: 'flex-end',
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
        transition: theme.transitions.create('margin', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        marginLeft: -drawerWidth,
    },
    contentShift: {
        transition: theme.transitions.create('margin', {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
        marginLeft: 0,
    },
    navRight: {
        marginLeft: 'auto'
    },
    navbarHeader: {
        marginTop: '14px'
    },
    mainInfo: {
        display: 'flex',
        flexDirection: 'row'
    },
    profileInfo: {
        color: 'black'
    },
    profileImage: {
        width: '60%'
    }
}));

export default function PersistentDrawerLeft() {
    const classes = useStyles();
    const theme = useTheme();
    const [open, setOpen] = React.useState(false);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    return (
        <Router>
        <div className={classes.root}>
            <CssBaseline />
            <AppBar
                position="fixed"
                className={clsx(classes.appBar, {
                    [classes.appBarShift]: open,
                })}
            >
                <Toolbar>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        onClick={handleDrawerOpen}
                        edge="start"
                        className={clsx(classes.menuButton, open && classes.hide)}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" noWrap className={classes.navRight}>
                        <div className={classes.navbarHeader}>
                            <div className="navbar-brand myDetails">
                                <p className='text-small myEmail'>Dheeraj Bharsiya</p>
                                <p className='text-small'>
                                    <a href="mailto:dheerajbharsiya@gmail.com">dheerajbharsiya@gmail.com</a>
                                </p>
                            </div>
                        </div>
                    </Typography>
                </Toolbar>
            </AppBar>
            <Drawer
                className={classes.drawer}
                variant="persistent"
                anchor="left"
                open={open}
                classes={{
                    paper: classes.drawerPaper,
                }}
            >
                <div className={classes.drawerHeader}>
                    <IconButton onClick={handleDrawerClose}>
                        {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
                    </IconButton>
                </div>
                <Divider />
                <List>
                    {[{ name: 'Profile', value: '/profile' }, { name: 'Resume', value: '/resume' }, { name: 'Contact Me', value: '/home' }].map((text, index) => (
                        <ListItem button key={text.name}>
                            <Link to={text.value}>
                                <ListItemIcon>{<ArrowForwardIcon />}</ListItemIcon>
                                <ListItemText primary={text.name} />
                            </Link>
                        </ListItem>
                    ))}
                </List>
                <Divider />
                {/* <List>
                    {['Learnings', 'Github', 'Stackoverflow', 'Linkedin'].map((text, index) => (
                        <ListItem button key={text}>
                            <ListItemIcon>{<ArrowForwardIcon />}</ListItemIcon>
                            <ListItemText primary={text} />
                        </ListItem>
                    ))}
                </List> */}
            </Drawer>
            <main
                className={clsx(classes.content, {
                    [classes.contentShift]: open,
                }, 'main-content')}
            >
                
                    <Switch >
                        <Route exact path="/" component={Profile} />
                        <Route path="/resume" component={Resume} />
                        <Route path="/profile" component={Profile} />
                        <Route path="/create-article" component={CreateTopics} />
                        <Route path="/home" component={Home} />
                        <Route component={NotFound} />
                    </Switch>
                
            </main>
        </div>
        </Router>
    );
}
