import React from 'react'
import { connect } from "react-redux";
import { saveArticle } from './actions/index';

const mapStateToProps = state => {
  return { articles: state.articles };
};

function mapDispatchToProps(dispatch) {
  return {
    saveArticle: article => dispatch(saveArticle(article))
  };
}


class ConnectedTopics extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
        articles: []
    }
  }

  componentDidMount() {
    fetch("https://dheerajbharsiyanode.herokuapp.com/helloWorld")
    .then(res => res.json())
    .then(
      (result) => {
        this.props.saveArticle(result);
        console.log(result);
        this.setState({
          articles: result
        });
      },
      (error) => {
        this.setState({
          isLoaded: true,
          error
        });
      });
  }

  render() {
    console.log(this.props);
    console.log(this.state);
    const {articles} = this.props;
    return (
      <ul className="list-group list-group-flush">
      {articles.map(el => (
        <li className="list-group-item" key={el._id}>
          {el.articleHeading}
        </li>
      ))}
      </ul>
    )
  }

}

const Topics = connect(mapStateToProps, mapDispatchToProps)(ConnectedTopics);
export default Topics